.showcase {
  min-height: 100vh;

  background: rgba(0, 0, 0, 0.2) url("/images/mountainsky.jpg") no-repeat fixed
    center;
  background-blend-mode: multiply;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.showcase * {
  color: white;
}
.showcase h2 {
  font-size: clamp(1.5rem, 3vw, 5rem);
}
.showcase h1 {
  font-size: clamp(2rem, 4vw, 7rem);
  font-weight: bold;
}
.showcase .ant-carousel .slick-slider .slick-dots {
  display: none !important;
}

.showcase button {
  width: fit-content;
  background: transparent;
  border: solid 2px white;
  outline: none;
  cursor: pointer;
  padding: 0.5rem 2rem;
  margin-top: 2rem;
  transition: all 0.3s ease-out;
  font-size: large;
  font-weight: bold;
}
.showcase button:hover {
  background: #ddd;
  color: black;
}

.next {
  font-size: 2rem;
  position: absolute;
  bottom: 10px;
  text-align: center;
}
.next span {
  transition: all 300ms ease-out;
}

.next :nth-child(2) {
  position: absolute;
  transform: translate(-95px, -30px);
  opacity: 0;
  visibility: hidden;
}

.next:hover :first-child {
  transform: translateY(20px);
  opacity: 0;
  visibility: hidden;
  transition: all 300ms ease-out;
}
.next:hover :nth-child(2) {
  transform: translate(-95px, 0);
  opacity: 1;
  visibility: visible;
}

.price {
  position: absolute;
  background: #ddd;
  bottom: 70px;
  right: 10px;
  padding: 5px 15px;
  border-radius: 1rem;
}

.ant-card-cover .ant-carousel img {
  height: 300px;
  max-height: 300px;
}

.landingCard {
  overflow: hidden;
}

.cardCover .cardDescription {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 1rem;
  transform: translateY(-100%);
  background: rgba(0, 0, 0, 0.7);
  opacity: 0;
  visibility: hidden;
  transition: 0.3s ease-out;
  background-blend-mode: multiply;
}

.cardCover .cardDescription h2,
.cardCover .cardDescription h4 {
  transition: 1s ease-out;
  opacity: 0;
  color: white;
}
.cardCover .cardDescription h2 {
  transform: translateX(-10px);
  transition-delay: 0.2s;
}
.cardCover .cardDescription h4 {
  transform: translateX(-5px);
  transition-delay: 0.4s;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 6; /* number of lines to show */
  -webkit-box-orient: vertical;
}

.cardCover:hover .cardDescription {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.cardCover:hover h2,
.cardCover:hover h4 {
  transform: translateX(0);
  opacity: 1;
}

.book {
  color: #01b5c3;
}

.searchCard .ant-card-body {
  padding: 0.5rem;
}

.choose {
  text-align: center;
  font-size: clamp(0.8rem, 2vw, 1.7rem);
}

.ant-divider-inner-text {
  transform: translateY(7px);
}

.ant-divider-horizontal.ant-divider-with-text::before,
.ant-divider-horizontal.ant-divider-with-text::after {
  border-top: 2px solid gray !important;
}
